.menu {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  height: 1080px;
  width: 1920px;
  padding: 50px 0 0 50px;
  bottom: 420px;
  left: -420px;
  -webkit-transform: rotateZ(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotateZ(-90deg);
  font-size: 30px;
  color: darkviolet;
  background: rgba(255,255,255,1);
  background: -webkit-gradient(left top, right top, color-stop(28%, rgba(255,255,255,1)), color-stop(100%, rgba(255,255,255,0)));
  background: -webkit-linear-gradient(left, rgba(255,255,255,1) 28%, rgba(255,255,255,0) 100%);
  background: -o-linear-gradient(left, rgba(255,255,255,1) 28%, rgba(255,255,255,0) 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(28%, rgba(255,255,255,1)), to(rgba(255,255,255,0)));
  background: linear-gradient(to right, rgba(255,255,255,1) 28%, rgba(255,255,255,0) 100%);
}

